"use client";

import React, { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Textarea } from '@/components/ui/textarea';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Edit, Save, X, Wand2 } from 'lucide-react';

interface PageEditorProps {
  initialContent: string;
  pageType: 'vendas' | 'remarketing';
  onSave: (newContent: string) => void;
  onClose: () => void;
}

export default function PageEditor({ initialContent, pageType, onSave, onClose }: PageEditorProps) {
  const [isEditing, setIsEditing] = useState(false);
  const [prompt, setPrompt] = useState('');
  const [editedContent, setEditedContent] = useState(initialContent);
  const [isGenerating, setIsGenerating] = useState(false);

  const handleGenerateWithPrompt = async () => {
    if (!prompt.trim()) {
      alert('Digite um prompt para editar a p√°gina');
      return;
    }

    setIsGenerating(true);
    
    try {
      // Simular gera√ß√£o com IA baseada no prompt
      // Em uma implementa√ß√£o real, aqui seria feita a chamada para uma API de IA
      await new Promise(resolve => setTimeout(resolve, 2000));
      
      // Aplicar modifica√ß√µes baseadas no prompt
      let newContent = editedContent;
      
      // Exemplos de modifica√ß√µes baseadas em prompts comuns
      if (prompt.toLowerCase().includes('urg√™ncia') || prompt.toLowerCase().includes('urgente')) {
        newContent = newContent.replace(/24 horas/g, '6 HORAS APENAS');
        newContent = newContent.replace(/ATEN√á√ÉO:/g, 'üö® URGENTE:');
      }
      
      if (prompt.toLowerCase().includes('desconto') || prompt.toLowerCase().includes('promo√ß√£o')) {
        newContent = newContent.replace(/R\$ (\d+,\d+)/g, (match, price) => {
          const numPrice = parseFloat(price.replace(',', '.'));
          const discountPrice = (numPrice * 0.7).toFixed(2).replace('.', ',');
          return `~~${match}~~ R$ ${discountPrice}`;
        });
      }
      
      if (prompt.toLowerCase().includes('benef√≠cio') || prompt.toLowerCase().includes('vantagem')) {
        newContent = newContent.replace('## O Que Voc√™ Vai Conquistar:', 
          `## O Que Voc√™ Vai Conquistar:\n\n‚úÖ **NOVO BENEF√çCIO**: ${prompt}\n`);
      }
      
      if (prompt.toLowerCase().includes('t√≠tulo') || prompt.toLowerCase().includes('headline')) {
        const lines = newContent.split('\n');
        if (lines[0].startsWith('#')) {
          lines[0] = `# ${prompt}`;
          newContent = lines.join('\n');
        }
      }
      
      if (prompt.toLowerCase().includes('garantia')) {
        newContent = newContent.replace('30 dias', '60 dias');
        newContent = newContent.replace('GARANTIA INCONDICIONAL', 'GARANTIA TOTAL E INCONDICIONAL');
      }
      
      // Adicionar modifica√ß√£o personalizada baseada no prompt
      if (prompt.toLowerCase().includes('adicionar') || prompt.toLowerCase().includes('incluir')) {
        newContent += `\n\n---\n\n## üéØ ATUALIZA√á√ÉO ESPECIAL\n\n${prompt}\n\n*Modifica√ß√£o aplicada via prompt de edi√ß√£o*`;
      }
      
      setEditedContent(newContent);
      setPrompt('');
      
    } catch (error) {
      console.error('Erro ao gerar conte√∫do:', error);
      alert('Erro ao processar o prompt. Tente novamente.');
    } finally {
      setIsGenerating(false);
    }
  };

  const handleSave = () => {
    onSave(editedContent);
    alert('P√°gina atualizada com sucesso!');
    onClose();
  };

  return (
    <div className="fixed inset-0 bg-black/80 backdrop-blur-sm z-50 flex items-center justify-center p-4">
      <div className="bg-gradient-to-br from-gray-900 to-black border border-red-900/30 rounded-lg w-full max-w-6xl h-[90vh] flex">
        {/* Painel de Edi√ß√£o */}
        <div className="w-1/3 border-r border-red-900/30 p-6 flex flex-col">
          <div className="flex items-center justify-between mb-6">
            <h2 className="text-2xl font-bold text-white">
              <Edit className="inline h-6 w-6 mr-2" />
              Editor de P√°gina
            </h2>
            <Button
              onClick={onClose}
              variant="outline"
              size="icon"
              className="border-red-900/30 bg-black/30 text-red-200 hover:bg-red-900/20"
            >
              <X className="h-4 w-4" />
            </Button>
          </div>

          <Card className="mb-6 border-red-900/20 bg-black/40">
            <CardHeader>
              <CardTitle className="text-white text-lg">
                <Wand2 className="inline h-5 w-5 mr-2" />
                Edi√ß√£o com IA
              </CardTitle>
              <CardDescription className="text-red-100/80">
                Digite um prompt para modificar a p√°gina automaticamente
              </CardDescription>
            </CardHeader>
            <CardContent>
              <Textarea
                value={prompt}
                onChange={(e) => setPrompt(e.target.value)}
                placeholder="Ex: Adicionar mais urg√™ncia na oferta, mudar o t√≠tulo para algo mais impactante, incluir garantia de 60 dias..."
                className="bg-black/30 border-red-900/30 text-white placeholder:text-red-200/50 min-h-[120px] mb-4"
              />
              <Button
                onClick={handleGenerateWithPrompt}
                disabled={isGenerating || !prompt.trim()}
                className="w-full bg-gradient-to-r from-purple-600 to-purple-700 hover:from-purple-700 hover:to-purple-800"
              >
                {isGenerating ? (
                  <>
                    <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white mr-2"></div>
                    Aplicando Modifica√ß√µes...
                  </>
                ) : (
                  <>
                    <Wand2 className="h-4 w-4 mr-2" />
                    Aplicar Prompt
                  </>
                )}
              </Button>
            </CardContent>
          </Card>

          <div className="space-y-4">
            <h3 className="text-lg font-semibold text-white">Exemplos de Prompts:</h3>
            <div className="space-y-2 text-sm">
              <div className="bg-black/30 p-3 rounded border border-red-900/20">
                <p className="text-red-300 font-medium">Urg√™ncia:</p>
                <p className="text-gray-300">"Adicionar mais urg√™ncia na oferta"</p>
              </div>
              <div className="bg-black/30 p-3 rounded border border-red-900/20">
                <p className="text-red-300 font-medium">Desconto:</p>
                <p className="text-gray-300">"Aplicar desconto de 30% no pre√ßo"</p>
              </div>
              <div className="bg-black/30 p-3 rounded border border-red-900/20">
                <p className="text-red-300 font-medium">T√≠tulo:</p>
                <p className="text-gray-300">"Mudar t√≠tulo para: Transforme Sua Vida Hoje"</p>
              </div>
              <div className="bg-black/30 p-3 rounded border border-red-900/20">
                <p className="text-red-300 font-medium">Garantia:</p>
                <p className="text-gray-300">"Aumentar garantia para 60 dias"</p>
              </div>
            </div>
          </div>

          <div className="mt-auto pt-6">
            <Button
              onClick={handleSave}
              className="w-full bg-gradient-to-r from-green-600 to-green-700 hover:from-green-700 hover:to-green-800"
            >
              <Save className="h-4 w-4 mr-2" />
              Publicar Altera√ß√µes
            </Button>
          </div>
        </div>

        {/* Preview da P√°gina */}
        <div className="flex-1 p-6">
          <div className="h-full bg-white rounded-lg overflow-hidden">
            <div className="h-full overflow-y-auto p-8">
              <div className="prose prose-lg max-w-none">
                {editedContent.split('\n').map((line, index) => {
                  if (line.startsWith('# ')) {
                    return <h1 key={index} className="text-4xl font-bold text-gray-900 mb-6">{line.substring(2)}</h1>;
                  }
                  if (line.startsWith('## ')) {
                    return <h2 key={index} className="text-3xl font-bold text-gray-800 mb-4 mt-8">{line.substring(3)}</h2>;
                  }
                  if (line.startsWith('### ')) {
                    return <h3 key={index} className="text-2xl font-bold text-gray-700 mb-3 mt-6">{line.substring(4)}</h3>;
                  }
                  if (line.startsWith('‚Ä¢ ')) {
                    return <li key={index} className="text-gray-700 mb-2">{line.substring(2)}</li>;
                  }
                  if (line.startsWith('‚úÖ ')) {
                    return <div key={index} className="bg-green-100 p-3 rounded-lg mb-3 text-green-800 font-medium">{line}</div>;
                  }
                  if (line.startsWith('‚ö° ') || line.startsWith('üö® ')) {
                    return <div key={index} className="bg-red-100 p-3 rounded-lg mb-3 text-red-800 font-bold text-center">{line}</div>;
                  }
                  if (line.startsWith('üéÅ ')) {
                    return <div key={index} className="bg-yellow-100 p-3 rounded-lg mb-3 text-yellow-800 font-medium">{line}</div>;
                  }
                  if (line.startsWith('üîí ')) {
                    return <div key={index} className="bg-blue-100 p-3 rounded-lg mb-3 text-blue-800 font-medium">{line}</div>;
                  }
                  if (line.includes('**') && line.includes('**')) {
                    const parts = line.split('**');
                    return (
                      <p key={index} className="text-gray-700 mb-4">
                        {parts.map((part, i) => 
                          i % 2 === 1 ? <strong key={i} className="font-bold text-gray-900">{part}</strong> : part
                        )}
                      </p>
                    );
                  }
                  if (line.trim() === '---') {
                    return <hr key={index} className="my-8 border-gray-300" />;
                  }
                  if (line.trim() === '') {
                    return <br key={index} />;
                  }
                  return <p key={index} className="text-gray-700 mb-4">{line}</p>;
                })}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
